.header {
  background-color: #0D47A1;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .logo {
  color: #fff;
  font-size: 24px;
  font-weight: bold;
}

.header nav ul {
  display: flex;
  gap: 20px;
  list-style: none;
}

.header nav ul li a {
  color: white;
  text-decoration: none;
  font-weight: 500;
}

.header nav ul li a:hover {
  text-decoration: underline;
}
