.projects-page {
  text-align: center;
  padding: 20px;
}

.project-card {
  border: 1px solid #ddd;
  padding: 20px;
  margin: 15px;
  border-radius: 8px;
  background-color: white;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.project-card h3 {
  color: #0D47A1;
}

.project-card a {
  color: #0D47A1;
  text-decoration: none;
  font-weight: bold;
}

.project-card a:hover {
  text-decoration: underline;
}
